package fry.lab.app.app.widgets

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.ExperimentalLayoutApi
import androidx.compose.foundation.layout.FlowRow
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.width
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import fry.lab.app.views.BlueButton
import fry_lab_app.composeapp.generated.resources.Res
import fry_lab_app.composeapp.generated.resources.ic_cyclone
import kotlinx.collections.immutable.ImmutableList
import kotlinx.collections.immutable.toImmutableList
import org.jetbrains.compose.resources.DrawableResource

@Composable
fun StyleBlueButtons(modifier: Modifier = Modifier) {

    val firstRowButtons = StyleBlueButtons.entries.toTypedArray().take(2).toImmutableList()
    val secondRowButtons = StyleBlueButtons.entries.drop(2).take(3).toImmutableList()
    val remainingButtons = StyleBlueButtons.entries.drop(5).toImmutableList()

    Column(modifier = modifier.width(400.dp)) {
        BlueRow(firstRowButtons)
        BlueRow(secondRowButtons)
        BlueRow(remainingButtons)
    }
}


@OptIn(ExperimentalLayoutApi::class)
@Composable
private fun BlueRow(buttonList: ImmutableList<StyleBlueButtons>) {
    FlowRow(
        modifier = Modifier.fillMaxWidth(),
        horizontalArrangement = Arrangement.Center
    ) {
        buttonList.forEach { buttonItem ->
            BlueButton(
                text = buttonItem.imgLabel,
                onClick = { }
            )
        }
    }
}

private enum class StyleBlueButtons(val imgLabel: String, val imgPath: DrawableResource) {
    STYLES(imgLabel = "Styles", imgPath = Res.drawable.ic_cyclone),
    LIGHTING(imgLabel = "Lighting", imgPath = Res.drawable.ic_cyclone),
    CAMERA(imgLabel = "Camera", imgPath = Res.drawable.ic_cyclone),
    ARTISTS(imgLabel = "Artists", imgPath = Res.drawable.ic_cyclone),
    COLORS(imgLabel = "Colors", imgPath = Res.drawable.ic_cyclone),
    MATERIALS(imgLabel = "Materials", imgPath = Res.drawable.ic_cyclone),
}
